/* Global Layout */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #f4f7ff, #e8ecf9);
  color: #333;
}

/* App Container */
.App {
  text-align: center;
  background: white;
  padding: 30px;
  max-width: 600px;
  margin: 40px auto;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
}

/* Textarea */
textarea {
  width: 100%;
  max-width: 100%;
  min-height: 100px;
  font-size: 1rem;
  font-family: 'Segoe UI', sans-serif;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fafafa;
  resize: vertical;
  box-sizing: border-box;
  margin-top: 10px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

textarea:focus {
  outline: none;
  border-color: #7f5af0;
  box-shadow: 0 0 0 3px rgba(127, 90, 240, 0.15);
  background-color: #fff;
}

textarea::placeholder {
  color: #888;
  font-style: italic;
}

/* Labels */
label {
  font-size: 0.95em;
  margin: 10px;
  display: inline-block;
}

/* Buttons (all) */
button {
  padding: 8px 12px;
  margin-top: 12px;
  background-color: #7f5af0;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background 0.2s ease;
}

button:hover {
  background-color: #6245cc;
}

/* Spinner */
.spinner {
  font-size: 1.2em;
  color: #444;
  margin-top: 10px;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { opacity: 0.3; }
  50% { opacity: 1; }
  100% { opacity: 0.3; }
}

/* Result Display */
.result-box {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 12px;
}

.result-text {
  background: #f4f4f4;
  padding: 12px;
  border-radius: 6px;
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-word;
  overflow-x: auto;
  text-align: left;
}

/* Button Group */
.button-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

/* Responsive layout on larger screens */
@media (min-width: 600px) {
  .result-box {
    flex-direction: row;
    align-items: center;
  }

  .result-text {
    flex: 1;
    margin-right: 10px;
  }

  .button-group {
    flex-shrink: 0;
  }
}
